import React, { useCallback, useMemo, useState } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import {
  Button,
  Container,
  Divider,
  Form,
  Grid,
  Header,
  Label,
  Modal,
  TextArea,
} from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import database from './database.json';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const DATABASE = {
  array: database,
  set: new Set(database),
};

const FORMATS_MAP = [
  ['anagrams', '–ê–Ω–∞–≥—Ä–∞–º–º—ã'],
  ['brukvas', '–ë—Ä—é–∫–≤—ã'],
  ['hybrids2', '–ì–∏–±—Ä–∏–¥—ã (2)'],
  ['hybrids3', '–ì–∏–±—Ä–∏–¥—ã (3)'],
  ['hybrids4', '–ì–∏–±—Ä–∏–¥—ã (4)'],
  ['golovahvosts', '–ì–æ–ª–æ–≤–∞—Ö–≤–æ—Å—Ç—ã'],
  ['zerkala', '–ó–µ—Ä–∫–∞–ª–∞'],
  ['logogrifs', '–õ–æ–≥–æ–≥—Ä–∏—Ñ—ã'],
  ['logogrifs2', '–õ–æ–≥–æ–≥—Ä–∏—Ñ—ã +2'],
  ['logogrifs3', '–õ–æ–≥–æ–≥—Ä–∏—Ñ—ã +3'],
  ['metagrams', '–ú–µ—Ç–∞–≥—Ä–∞–º–º—ã'],
  ['metagrifs', '–ú–µ—Ç–∞–≥—Ä–∏—Ñ—ã'],
  ['metanagrams', '–ú–µ—Ç–∞–Ω–∞–≥—Ä–∞–º–º—ã'],
  ['plusograms', '–ü–ª—é—Å–æ–≥—Ä–∞–º–º—ã'],
  ['tlenograms', '–¢–ª–µ–Ω–æ–≥—Ä–∞–º–º—ã'],
  ['tikvas', '–¢—ã–∫–≤—ã'],
  ['sharadas', '–®–∞—Ä–∞–¥—ã'],
];

const isZerkalo = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  return word1 === word2.split('').reverse().join('');
};

const isAnagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  return word1Sorted === word2Sorted;
};

const isPlusogram = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 1) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  let hasDifference = false;
  for (let i = 0; i < word2Sorted.length; i++) {
    if (word1Sorted.charAt(i - +hasDifference) !== word2Sorted.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
};

const isMetagram = (word1, word2) => {
  if (word1.length === 1 || word1.length !== word2.length) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word1.length; i++) {
    if (word1.charAt(i) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
};

const isMetanagram = (word1, word2) => {
  if (word1.length === 1 || word1.length !== word2.length) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 1;
};

const isTlenogram = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 1) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 2;
};

const isLogogrif = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 1) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +hasDifference) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
};

const isLogogrif2 = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 2) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
};

const isLogogrif3 = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 3) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1 || totalDifferences === 2) {
        return false;
      }
    } else {
      if (totalDifferences === 3) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 3;
};

const isMetagrif = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) !== word2.charAt(i)) {
      totalDifferences++;

      if (totalDifferences > 2) {
        break;
      }
    }
  }

  if (totalDifferences === 2) {
    return true;
  }

  totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +(totalDifferences === 2)) !== word2.charAt(i)) {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
};

const isBrukva = (word1, word2) => {
  if (word1.length === 1 || word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
};

const isTikva = (word1, word2) => {
  if (word1.length <= 2 || word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1 || totalDifferences === 2) {
        return false;
      }
    } else {
      if (totalDifferences === 3) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 3;
};

const createHybrid2 = (word1, word2) => {
  if (word1.length <= 2 || word2.length <= 2) {
    return null;
  }

  const intersection = word1.slice(-2);
  if (word2.slice(0, 2) === intersection) {
    return word1.slice(0, -2) + intersection.toUpperCase() + word2.slice(2);
  }

  return null;
};

const createHybrid3 = (word1, word2) => {
  if (word1.length <= 3 || word2.length <= 3) {
    return null;
  }

  const intersection = word1.slice(-3);
  if (word2.slice(0, 3) === intersection) {
    return word1.slice(0, -3) + intersection.toUpperCase() + word2.slice(3);
  }

  return null;
};

const createHybrid4 = (word1, word2) => {
  if (word1.length <= 4 || word2.length <= 4) {
    return null;
  }

  const intersection = word1.slice(-4);
  if (word2.slice(0, 4) === intersection) {
    return word1.slice(0, -4) + intersection.toUpperCase() + word2.slice(4);
  }

  return null;
};

const createSharada = (word1, word2, dictionarySet) => {
  const word = word1 + word2;
  if (dictionarySet.has(word)) {
    return word;
  }

  return null;
};

const createGolovahvosts = (word1, word2, dictionarySet) => {
  if (word1.length <= 3 || word2.length <= 3) {
    return [];
  }

  const golovahvosts = [];
  for (let i = word1.length - 1; i >= 3; i--) {
    const word1Tail = word1.slice(-i);

    for (let j = word2.length - 1; j >= 3; j--) {
      const word = word1Tail + word2.slice(0, j);
      if (dictionarySet.has(word)) {
        golovahvosts.push(
          word1.slice(0, word1.length - i) + word.toUpperCase() + word2.slice(j - word2.length),
        );
      }
    }
  }

  return golovahvosts;
};

const getFormats = (wordGroups, dictionarySet, selectedFormatsSet) => {
  const formats = FORMATS_MAP.reduce((result, [format]) => {
    if (selectedFormatsSet.has(format)) {
      return {
        ...result,
        [format]: [],
      };
    }

    return result;
  }, {});

  wordGroups.forEach((wordGroup1, i) => {
    wordGroups.forEach((wordGroup2, j) => {
      if (j === i) {
        return;
      }

      wordGroup1.forEach((word1) => {
        wordGroup2.forEach((word2) => {
          if (word2 === word1) {
            return;
          }

          const result = `${word1} ${word2}`;

          if (j > i) {
            if (formats.zerkala && !formats.zerkala.includes(result) && isZerkalo(word1, word2)) {
              formats.zerkala.push(result);
            }

            if (formats.anagrams && !formats.anagrams.includes(result) && isAnagram(word1, word2)) {
              formats.anagrams.push(result);
            }

            if (
              formats.metagrams &&
              !formats.metagrams.includes(result) &&
              isMetagram(word1, word2)
            ) {
              formats.metagrams.push(result);
            }

            if (
              formats.metanagrams &&
              !formats.metanagrams.includes(result) &&
              isMetanagram(word1, word2)
            ) {
              formats.metanagrams.push(result);
            }
          }

          if (
            formats.plusograms &&
            !formats.plusograms.includes(result) &&
            isPlusogram(word1, word2)
          ) {
            formats.plusograms.push(result);
          }

          if (
            formats.tlenograms &&
            !formats.tlenograms.includes(result) &&
            isTlenogram(word1, word2)
          ) {
            formats.tlenograms.push(result);
          }

          if (
            formats.logogrifs &&
            !formats.logogrifs.includes(result) &&
            isLogogrif(word1, word2)
          ) {
            formats.logogrifs.push(result);
          }

          if (
            formats.logogrifs2 &&
            !formats.logogrifs2.includes(result) &&
            isLogogrif2(word1, word2)
          ) {
            formats.logogrifs2.push(result);
          }

          if (
            formats.logogrifs3 &&
            !formats.logogrifs3.includes(result) &&
            isLogogrif3(word1, word2)
          ) {
            formats.logogrifs3.push(result);
          }

          if (
            formats.metagrifs &&
            !formats.metagrifs.includes(result) &&
            isMetagrif(word1, word2)
          ) {
            formats.metagrifs.push(result);
          }

          if (formats.brukvas && !formats.brukvas.includes(result) && isBrukva(word1, word2)) {
            formats.brukvas.push(result);
          }

          if (formats.tikvas && !formats.tikvas.includes(result) && isTikva(word1, word2)) {
            formats.tikvas.push(result);
          }

          if (formats.hybrids2) {
            const hybrid2 = createHybrid2(word1, word2);
            if (hybrid2 && !formats.hybrids2.includes(hybrid2)) {
              formats.hybrids2.push(hybrid2);
            }
          }

          if (formats.hybrids3) {
            const hybrid3 = createHybrid3(word1, word2);
            if (hybrid3 && !formats.hybrids3.includes(hybrid3)) {
              formats.hybrids3.push(hybrid3);
            }
          }

          if (formats.hybrids4) {
            const hybrid4 = createHybrid4(word1, word2);
            if (hybrid4 && !formats.hybrids4.includes(hybrid4)) {
              formats.hybrids4.push(hybrid4);
            }
          }

          if (formats.sharadas) {
            const sharada = createSharada(word1, word2, dictionarySet);
            if (sharada && !formats.sharadas.includes(sharada)) {
              formats.sharadas.push(sharada);
            }
          }

          if (formats.golovahvosts) {
            const golovahvosts = createGolovahvosts(word1, word2, dictionarySet);
            golovahvosts.forEach((golovahvost) => {
              if (!formats.golovahvosts.includes(golovahvost)) {
                formats.golovahvosts.push(golovahvost);
              }
            });
          }
        });
      });
    });
  });

  return formats;
};

const App = () => {
  const [selectedFormatsSet, setSelectedFormatsSet] = useState(new Set());

  const [wordGroups, handleWordGroupsChange] = useField('');
  const [wordGroupsDebounced] = useDebounce(wordGroups, 500);

  const wordGroupsClean = useMemo(
    () =>
      wordGroupsDebounced.split(/\t+|\n+/).flatMap((wordGroup) => {
        wordGroup = wordGroup.trim();

        if (!wordGroup) {
          return [];
        }

        const words = wordGroup.replaceAll('/', ' ').replaceAll('\\', ' ').split(/\s+/);

        return [
          words.flatMap((word) => {
            if (!/^\p{L}+$/u.test(word)) {
              return [];
            }

            return [word.toLowerCase().replaceAll('—ë', '–µ')];
          }),
        ];
      }),
    [wordGroupsDebounced],
  );

  const formats = useMemo(() => {
    return getFormats(wordGroupsClean, DATABASE.set, selectedFormatsSet);
  }, [selectedFormatsSet, wordGroupsClean]);

  const handleSelectedFormatChange = useCallback(
    (_, { value }) => {
      if (selectedFormatsSet.has(value)) {
        setSelectedFormatsSet(
          new Set([...selectedFormatsSet].filter((format) => format !== value)),
        );
      } else {
        setSelectedFormatsSet(new Set([...selectedFormatsSet, value]));
      }
    },
    [selectedFormatsSet],
  );

  return (
    <>
      <div className={styles.header}>
        <Container>
          <Grid padded stackable>
            <Grid.Row>
              <Grid.Column width={15}>
                <Form>
                  <TextArea
                    autoFocus
                    value={wordGroups}
                    placeholder="–ì—Ä—É–ø–ø—ã —Å–ª–æ–≤"
                    onChange={handleWordGroupsChange}
                  />
                </Form>
              </Grid.Column>
              <Grid.Column>
                <Modal size="mini" trigger={<Button size="big" icon="setting" />}>
                  <Header icon="setting" content="–ù–∞—Å—Ç—Ä–æ–π–∫–∏" />
                  <Modal.Content>
                    <Form>
                      <Form.Group grouped>
                        <label>–§–æ—Ä–º–∞—Ç—ã</label>
                        {FORMATS_MAP.map(([format, formatLabel]) => (
                          <Form.Checkbox
                            key={format}
                            value={format}
                            label={formatLabel}
                            checked={selectedFormatsSet.has(format)}
                            onChange={handleSelectedFormatChange}
                          />
                        ))}
                      </Form.Group>
                    </Form>
                  </Modal.Content>
                </Modal>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </div>
      <div className={styles.content}>
        <Container>
          <Grid padded>
            {FORMATS_MAP.filter(([format]) => formats[format] && formats[format].length > 0).map(
              ([format, formatLabel]) => (
                <Grid.Row key={format}>
                  <Grid.Column>
                    <Divider horizontal>
                      <Header as="h4" className={styles.title}>
                        {formatLabel}
                      </Header>
                    </Divider>
                    <div className={styles.words}>
                      {formats[format].map((word, i) => (
                        <Label
                          horizontal
                          key={word}
                          className={classNames(styles.word, i % 2 && styles.odd)}
                        >
                          {word}
                        </Label>
                      ))}
                    </div>
                  </Grid.Column>
                </Grid.Row>
              ),
            )}
          </Grid>
        </Container>
      </div>
    </>
  );
};

export default withAuth(App);
